___CSS_LOADER_EXPORT___.push([module.id, "\n.example-avatar .avatar-upload .rounded-circle {\n  width: 200px;\n  height: 200px;\n}\n.example-avatar .text-center .btn {\n  margin: 0 .5rem\n}\n.example-avatar .avatar-edit-image {\n  max-width: 100%\n}\n.example-avatar .drop-active {\n  top: 0;\n  bottom: 0;\n  right: 0;\n  left: 0;\n  position: fixed;\n  z-index: 9999;\n  opacity: .6;\n  text-align: center;\n  background: #000;\n}\n.example-avatar .drop-active h3 {\n  margin: -.5em 0 0;\n  position: absolute;\n  top: 50%;\n  left: 0;\n  right: 0;\n  -webkit-transform: translateY(-50%);\n  -ms-transform: translateY(-50%);\n  transform: translateY(-50%);\n  font-size: 40px;\n  color: #fff;\n  padding: 0;\n}\n", "",{"version":3,"sources":["webpack://./docs/views/examples/Avatar.vue"],"names":[],"mappings":";AA4CA;EACE,YAAY;EACZ,aAAa;AACf;AACA;EACE;AAEF;AACA;EACE;AACF;AAGA;EACE,MAAM;EACN,SAAS;EACT,QAAQ;EACR,OAAO;EACP,eAAe;EACf,aAAa;EACb,WAAW;EACX,kBAAkB;EAClB,gBAAgB;AAClB;AAEA;EACE,iBAAiB;EACjB,kBAAkB;EAClB,QAAQ;EACR,OAAO;EACP,QAAQ;EACR,mCAAmC;EACnC,+BAA+B;EAC/B,2BAA2B;EAC3B,eAAe;EACf,WAAW;EACX,UAAU;AACZ","sourcesContent":["<template>\n  <div class=\"example-avatar\">\n    <div v-show=\"$refs.upload && $refs.upload.dropActive\" class=\"drop-active\">\n      <h3>Drop files to upload</h3>\n    </div>\n    <div class=\"avatar-upload\"  v-show=\"!edit\">\n      <div class=\"text-center p-2\">\n        <label for=\"avatar\">\n          <img :src=\"files.length ? files[0].url : 'https://www.gravatar.com/avatar/default?s=200&r=pg&d=mm'\"  class=\"rounded-circle\" />\n          <h4 class=\"pt-2\">or<br/>Drop files anywhere to upload</h4>\n        </label>\n      </div>\n      <div class=\"text-center p-2\">\n        <file-upload\n          extensions=\"gif,jpg,jpeg,png,webp\"\n          accept=\"image/png,image/gif,image/jpeg,image/webp\"\n          name=\"avatar\"\n          class=\"btn btn-primary\"\n          post-action=\"/upload/post\"\n          :drop=\"!edit\"\n          v-model=\"files\"\n          @input-filter=\"inputFilter\"\n          @input-file=\"inputFile\"\n          ref=\"upload\">\n          Upload avatar\n        </file-upload>\n      </div>\n    </div>\n\n    <div class=\"avatar-edit\" v-show=\"files.length && edit\">\n      <div class=\"avatar-edit-image\" v-if=\"files.length\">\n        <img ref=\"editImage\" :src=\"files[0].url\" />\n      </div>\n      <div class=\"text-center p-4\">\n        <button type=\"button\" class=\"btn btn-secondary\" @click.prevent=\"$refs.upload.clear\">Cancel</button>\n        <button type=\"submit\" class=\"btn btn-primary\" @click.prevent=\"editSave\">Save</button>\n      </div>\n    </div>\n    <div class=\"pt-5 source-code\">\n      Source code: <a href=\"https://github.com/lian-yue/vue-upload-component/blob/master/docs/views/examples/Avatar.vue\">/docs/views/examples/Avatar.vue</a>\n    </div>\n  </div>\n</template>\n<style>\n.example-avatar .avatar-upload .rounded-circle {\n  width: 200px;\n  height: 200px;\n}\n.example-avatar .text-center .btn {\n  margin: 0 .5rem\n\n}\n.example-avatar .avatar-edit-image {\n  max-width: 100%\n}\n\n\n.example-avatar .drop-active {\n  top: 0;\n  bottom: 0;\n  right: 0;\n  left: 0;\n  position: fixed;\n  z-index: 9999;\n  opacity: .6;\n  text-align: center;\n  background: #000;\n}\n\n.example-avatar .drop-active h3 {\n  margin: -.5em 0 0;\n  position: absolute;\n  top: 50%;\n  left: 0;\n  right: 0;\n  -webkit-transform: translateY(-50%);\n  -ms-transform: translateY(-50%);\n  transform: translateY(-50%);\n  font-size: 40px;\n  color: #fff;\n  padding: 0;\n}\n</style>\n\n\n<script>\nimport Cropper from 'cropperjs'\nimport FileUpload from 'vue-upload-component'\nexport default {\n  components: {\n    FileUpload,\n  },\n\n  data() {\n    return {\n      files: [],\n      edit: false,\n      cropper: false,\n    }\n  },\n\n  watch: {\n    edit(value) {\n      if (value) {\n        this.$nextTick(function () {\n          if (!this.$refs.editImage) {\n            return\n          }\n          let cropper = new Cropper(this.$refs.editImage, {\n            aspectRatio: 1 / 1,\n            viewMode: 1,\n          })\n          this.cropper = cropper\n        })\n      } else {\n        if (this.cropper) {\n          this.cropper.destroy()\n          this.cropper = false\n        }\n      }\n    }\n  },\n\n  methods: {\n    editSave() {\n      this.edit = false\n\n      let oldFile = this.files[0]\n\n      let binStr = atob(this.cropper.getCroppedCanvas().toDataURL(oldFile.type).split(',')[1])\n      let arr = new Uint8Array(binStr.length)\n      for (let i = 0; i < binStr.length; i++) {\n        arr[i] = binStr.charCodeAt(i)\n      }\n\n      let file = new File([arr], oldFile.name, { type: oldFile.type })\n\n      this.$refs.upload.update(oldFile.id, {\n        file,\n        type: file.type,\n        size: file.size,\n        active: true,\n      })\n    },\n\n    alert(message) {\n      alert(message)\n    },\n\n    inputFile(newFile, oldFile, prevent) {\n      if (newFile && !oldFile) {\n        this.$nextTick(function () {\n          this.edit = true\n        })\n      }\n      if (!newFile && oldFile) {\n        this.edit = false\n      }\n    },\n\n    inputFilter(newFile, oldFile, prevent) {\n      if (newFile && !oldFile) {\n        if (!/\\.(gif|jpg|jpeg|png|webp)$/i.test(newFile.name)) {\n          this.alert('Your choice is not a picture')\n          return prevent()\n        }\n      }\n\n      if (newFile && (!oldFile || newFile.file !== oldFile.file)) {\n        newFile.url = ''\n        let URL = window.URL || window.webkitURL\n        if (URL && URL.createObjectURL) {\n          newFile.url = URL.createObjectURL(newFile.file)\n        }\n      }\n    }\n  }\n}\n</script>\n"],"sourceRoot":""}]);
