___CSS_LOADER_EXPORT___.push([module.id, "\n.example-simple label.btn {\n  margin-bottom: 0;\n  margin-right: 1rem;\n}\n", "",{"version":3,"sources":["webpack://./docs/views/examples/Simple.vue"],"names":[],"mappings":";AA8CA;EACE,gBAAgB;EAChB,kBAAkB;AACpB","sourcesContent":["<template>\n  <div class=\"example-simple\">\n    <h1 id=\"example-title\" class=\"example-title\">Simple Example</h1>\n    <div class=\"upload\">\n      <ul>\n        <li v-for=\"file in files\" :key=\"file.id\">\n          <span>{{file.name}}</span> -\n          <span>{{$formatSize(file.size)}}</span> -\n          <span v-if=\"file.error\">{{file.error}}</span>\n          <span v-else-if=\"file.success\">success</span>\n          <span v-else-if=\"file.active\">active</span>\n          <span v-else-if=\"!!file.error\">{{file.error}}</span>\n          <span v-else></span>\n        </li>\n      </ul>\n      <div class=\"example-btn\">\n        <file-upload\n          class=\"btn btn-primary\"\n          post-action=\"/upload/post\"\n          extensions=\"gif,jpg,jpeg,png,webp\"\n          accept=\"image/png,image/gif,image/jpeg,image/webp\"\n          :multiple=\"true\"\n          :size=\"1024 * 1024 * 10\"\n          v-model=\"files\"\n          @input-filter=\"inputFilter\"\n          @input-file=\"inputFile\"\n          ref=\"upload\">\n          <i class=\"fa fa-plus\"></i>\n          Select files\n        </file-upload>\n        <button type=\"button\" class=\"btn btn-success\" v-if=\"!upload || !upload.active\" @click.prevent=\"upload.active = true\">\n          <i class=\"fa fa-arrow-up\" aria-hidden=\"true\"></i>\n          Start Upload\n        </button>\n        <button type=\"button\" class=\"btn btn-danger\"  v-else @click.prevent=\"upload.active = false\">\n          <i class=\"fa fa-stop\" aria-hidden=\"true\"></i>\n          Stop Upload\n        </button>\n      </div>\n    </div>\n    <div class=\"pt-5 source-code\">\n      Source code: <a href=\"https://github.com/lian-yue/vue-upload-component/blob/master/docs/views/examples/Simple.vue\">/docs/views/examples/Simple.vue</a>\n    </div>\n  </div>\n</template>\n<style>\n.example-simple label.btn {\n  margin-bottom: 0;\n  margin-right: 1rem;\n}\n</style>\n\n<script>\nimport {ref} from 'vue'\nimport FileUpload from 'vue-upload-component'\nexport default {\n  components: {\n    FileUpload,\n  },\n\n  setup(props, context) {\n    const upload = ref(null)\n    \n    const files = ref([])\n\n    function inputFilter(newFile, oldFile, prevent) {\n      if (newFile && !oldFile) {\n        // Before adding a file\n        // \n\n        // Filter system files or hide files\n        //  \n        if (/(\\/|^)(Thumbs\\.db|desktop\\.ini|\\..+)$/.test(newFile.name)) {\n          return prevent()\n        }\n        \n        // Filter php html js file\n        //  php html js \n        if (/\\.(php5?|html?|jsx?)$/i.test(newFile.name)) {\n          return prevent()\n        }\n      }\n    }\n\n    function inputFile(newFile, oldFile) {\n      if (newFile && !oldFile) {\n        // add\n        console.log('add', newFile)\n      }\n      if (newFile && oldFile) {\n        // update\n        console.log('update', newFile)\n      }\n\n      if (!newFile && oldFile) {\n        // remove\n        console.log('remove', oldFile)\n      }\n    }\n\n    return {\n      files,\n      upload,\n      inputFilter,\n      inputFile,\n    }\n  }\n}\n</script>\n"],"sourceRoot":""}]);
