___CSS_LOADER_EXPORT___.push([module.id, "\n.example-vuex label.btn {\n  margin-bottom: 0;\n  margin-right: 1rem;\n}\n", "",{"version":3,"sources":["webpack://./docs/views/examples/Vuex.vue"],"names":[],"mappings":";AA4CA;EACE,gBAAgB;EAChB,kBAAkB;AACpB","sourcesContent":["<template>\n  <div class=\"example-vuex\">\n    <h1 id=\"example-title\" class=\"example-title\">Vuex Example</h1>\n    <div class=\"upload\">\n      <ul>\n        <li v-for=\"file in files\" :key=\"file.id\">\n          <span>{{file.name}}</span> -\n          <span>{{$formatSize(file.size)}}</span> -\n          <span v-if=\"file.error\">{{file.error}}</span>\n          <span v-else-if=\"file.success\">success</span>\n          <span v-else-if=\"file.active\">active</span>\n          <span v-else></span>\n        </li>\n      </ul>\n      <div class=\"example-btn\">\n        <file-upload\n          class=\"btn btn-primary\"\n          post-action=\"/upload/post\"\n          extensions=\"gif,jpg,jpeg,png,webp\"\n          accept=\"image/png,image/gif,image/jpeg,image/webp\"\n          :multiple=\"true\"\n          :size=\"1024 * 1024 * 10\"\n          :value=\"files\"\n          @update:modelValue=\"inputUpdate\"\n          ref=\"upload\">\n          <i class=\"fa fa-plus\"></i>\n          Select files\n        </file-upload>\n        <button type=\"button\" class=\"btn btn-success\" v-if=\"!$refs.upload || !$refs.upload.active\" @click.prevent=\"$refs.upload.active = true\">\n          <i class=\"fa fa-arrow-up\" aria-hidden=\"true\"></i>\n          Start Upload\n        </button>\n        <button type=\"button\" class=\"btn btn-danger\"  v-else @click.prevent=\"$refs.upload.active = false\">\n          <i class=\"fa fa-stop\" aria-hidden=\"true\"></i>\n          Stop Upload\n        </button>\n      </div>\n    </div>\n    <div class=\"pt-5 source-code\">\n      Source code: <a href=\"https://github.com/lian-yue/vue-upload-component/blob/master/docs/views/examples/Vuex.vue\">/docs/views/examples/Vuex.vue</a>, <a href=\"https://github.com/lian-yue/vue-upload-component/blob/master/docs/store.js\">/docs/store.js</a>\n    </div>\n  </div>\n</template>\n<style>\n.example-vuex label.btn {\n  margin-bottom: 0;\n  margin-right: 1rem;\n}\n</style>\n\n<script>\nimport { mapState } from 'vuex'\nimport FileUpload from 'vue-upload-component'\nexport default {\n  components: {\n    FileUpload,\n  },\n\n  computed: {\n    ...mapState([\n      'files',\n    ])\n  },\n\n  methods: {\n    inputUpdate(files) {\n      this.$store.commit('updateFiles', files)\n    },\n  }\n}\n</script>\n"],"sourceRoot":""}]);
