px;\n  margin-left: -15px;\n  max-height: 100%;\n  overflow-y: auto;\n}\n#sidebar-nav .nav {\n  display: block;\n}\n\n#sidebar-nav .nav .nav-item .nav {\n  display: none;\n  margin-bottom: 1rem;\n}\n#sidebar-nav .nav .nav-item .nav {\n  display: none;\n  margin-bottom: 1rem;\n}\n\n#sidebar-nav .nav .nav-item.active .nav, #sidebar-nav .nav .active + .nav {\n  display: block;\n}\n\n@media (min-width: 768px) {\n  #sidebar-nav .nav .nav-item .nav {\n    display: block;\n  }\n}\n\n#sidebar-nav .nav .nav-link.active, #sidebar-nav .nav .active > .nav-link{\n  color: #262626;\n  font-weight: 500;\n}\n\n#sidebar-nav .nav-item .nav-link {\n  padding: .25rem 1rem;\n  font-weight: 500;\n  color: #666\n}\n\n#sidebar-nav .nav-item .nav-item .nav-link {\n  font-weight: 400;\n  font-size: 85%;\n  margin-left: 1rem\n}\n\n#main {\n  padding-top: 1rem;\n  margin-bottom: 2rem\n}\n\n\nblockquote {\n  margin-bottom: 1rem;\n  font-size: 1.25rem;\n  padding: 0 1em;\n  color: #6a737d;\n  border-left: 0.25em solid #dfe2e5;\n}\n\npre {\n  padding: 16px;\n  overflow: auto;\n  font-size: 85%;\n  line-height: 1.45;\n  background-color: #f6f8fa;\n  border-radius: 3px;\n}\n\n.modal-backdrop.fade {\n  visibility: hidden;\n}\n.modal-backdrop.fade.show {\n  visibility: visible;\n}\n\n.fade.show {\n  display: block;\n  z-index: 1072;\n}\n.source-code {\n  font-size: 2em;\n  font-weight: bold;\n  color: #f00;\n}\n</style>\n<script>\nexport default {\n  data() {\n    return {\n      showLocale: false,\n      showNav: false,\n    }\n  },\n  beforeCreate() {\n    if (this.$route.params.locale && this.$route.params.locale !== this.$i18n.locale) {\n      this.$i18n.locale = this.$route.params.locale\n    }\n  },\n  beforeUpdate() {\n    if (this.$route.params.locale && this.$route.params.locale !== this.$i18n.locale) {\n      this.$i18n.locale = this.$route.params.locale\n    }\n  },\n  computed: {\n    locale() {\n      let i18n = this.$i18n\n      return i18n.messages[i18n.locale].locale\n    },\n  },\n  methods: {\n    onLocale(show) {\n      if (show) {\n        this.showLocale = show\n      } else {\n        setTimeout(() => {\n          this.showLocale = show\n        }, 128)\n      }\n    }\n  }\n}\n</script>\n"],"sourceRoot":""}]);
